html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

* {
  box-sizing: border-box;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
  font-size: 16px;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

@font-face {
  font-family: BLWebHeader;
  src: url("../fonts/Futura-Heavy.otf") format("opentype");
}

@font-face {
  font-family: BLWeb;
  src: url("../fonts/Bemio.otf") format("opentype");
}

@font-face {
  font-family: BLWebItalic;
  src: url("../fonts/BemioItalic.otf") format("opentype");
}

@font-face {
  font-family: Futura;
  src: url("../fonts/Futura.ttc") format("truetype"), url("../fonts/Futura.ttf") format("truetype");
}

body {
  background-color: #ee717d !important;
  font-family: 'Futura';
}

img.bllogo {
  width: 5em;
  height: 5em;
}

.featured-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ee717d;
  font-size: 8em;
}

.topbottompadding {
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.center-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.alert-featured {
  background-color: #fad5ac;
  color: #14182e;
}

.yellow-bg {
  background-color: #fad5ac;
}

h1 {
  font-family: 'BLWebHeader';
  color: #14182e;
  font-size: 3rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-transform: uppercase;
}

h2 {
  font-family: 'BLWebHeader';
  color: #14182e;
  font-size: 2rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-transform: uppercase;
}

h3 {
  font-family: 'BLWebHeader';
  color: #14182e;
  font-size: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-transform: uppercase;
}

a {
  color: #14182e;
}

a:hover {
  color: #14182e;
}

#paintBrushBg {
  text-align: center;
  background: url("../images/MENYKNAPPBAK.png");
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
  padding: 15px;
  text-align: center;
  color: #fad5ac;
}

#paintBrushBg a {
  color: #fad5ac;
}

.v-header {
  height: 100vh;
  position: relative;
}

.v-container {
  padding-left: 0rem;
  padding-right: 0rem;
}

.fullscreen-video-wrap {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: -1;
}

.fullscreen-video-wrap video {
  min-height: 100%;
  min-width: 100%;
}
